import React, { useState, useRef } from 'react';
import style from './Messages.module.scss'
import formatDistanceToNow from 'date-fns/formatDistanceToNow'
import ruLocale from 'date-fns/locale/ru'

import avatar from '../../../assets/img/test.png'
import ReadCheker from '../../readCheker/ReadCheker';
import audioWave from '../../../assets/img/audioWave.svg'
import pauseSvg from '../../../assets/img/pause.svg'
import playSvg from '../../../assets/img/play.svg'


function Messages({ user, text, date, isMe, isRead, attachments, isTyping, audio }) {

    const duration = '12s'
    const audioRef = useRef(null)

    const [isPlay, setIsPlay] = useState(false)

    const togglePlay = () => {
        setIsPlay(!isPlay)
        if (!isPlay) {
            audioRef.current.play()
        } else {
            audioRef.current.pause()

        }
    }

    return (
        <div className={style.Messages + " " + `${isMe && style.me} ${audio && style.message_audio}`}>

            <div className={style.avatar}>
                <img src={user.avatar || avatar} alt={'avatar ' + user.fullName} />
            </div>

            <div className={style.buble}>
                {(audio || text || isTyping)
                    && <div
                        className={
                            !isTyping && style.buble__content + ' ' +
                            `${isMe && !audio
                                ? style.white
                                : style.blue}`}>

                        {text && text}
                        {isTyping && <div className={style.typing__indicator}>
                            <span />
                            <span />
                            <span />
                        </div>}
                        {audio && <>
                            <div className={style.play} style={{ animationDuration: duration, animationPlayState: isPlay ? 'running' : 'paused' }}></div>
                            <div className={style.audio__wrapper}>
                                <audio ref={audioRef} src={audio} preload onEnded={togglePlay} />
                                <div className={style.audio__btn}><button onClick={togglePlay}>
                                    <img src={!isPlay ? playSvg : pauseSvg} /></button></div>
                                <img className={style.audio__img} src={audioWave} />
                                <div className={style.audio__duration}>00:12</div>
                            </div></>}
                    </div>}



                {attachments &&
                    <div className={style.message__attachments}>

                        {attachments.map(item => (
                            <img src={item.url}
                                className={style.message__attachment + ' '
                                    + `${(attachments.length === 1 && !text)
                                    && style.message__attachment_one}`} alt={item.filename}

                            />))}
                    </div>}




                {date && <div className={style.date}>{formatDistanceToNow(date, { addSuffix: true, locale: ruLocale })}</div>}

            </div>
            <ReadCheker isMe={isMe} isRead={isRead} />


        </div>
    )
}



export default Messages;
